// IMPORTS
import Throttled from '../../js/lib/throttled';

(function() {
  'use strict';
  // Set Hero Height to fulscreen size
  let maxHeroHeight = () => {
    let hero = document.getElementById('hero'),
      windowHeight = window.innerHeight + 'px';

    return hero.style.height = windowHeight;
  }

  window.addEventListener('load', maxHeroHeight);
  window.addEventListener('resize', () => Throttled(maxHeroHeight(), 400));
})();



